import {RouterModule} from '@angular/router';
import {
  ProductosMainComponent,
  ProductoComponent,
  ProductosInventarioComponent,
  ProductosListaComponent,
  ProductosPanelComponent,
} from './'

const routes = [{
  path: '',
  component: ProductosMainComponent,
  children: [{
    path: 'panel',
    component: ProductosPanelComponent
  }, {
    path: 'lista',
    component: ProductosListaComponent
  }, {
    path: 'inventario',
    component: ProductosInventarioComponent
  }, {
    path: 'producto',
    component: ProductoComponent
  }]
}]

export const ProductosRoutes = RouterModule.forChild(routes)



