import {NgModule} from '@angular/core'
import { FormsModule } from '@angular/forms'
import { CommonModule } from '@angular/common'

import {
  ProductosRoutes,
  ProductosMainComponent,
  ProductoComponent,
  ProductosInventarioComponent,
  ProductosListaComponent,
  ProductosPanelComponent
} from './'

@NgModule({
  imports: [
    FormsModule,
    CommonModule,
    ProductosRoutes
  ],
  declarations: [
    ProductosMainComponent,
    ProductoComponent,
    ProductosInventarioComponent,
    ProductosListaComponent,
    ProductosPanelComponent,
  ]
})
export class ProductosModule {
  public static routes = ProductosRoutes
}



